<div class="row"> <div class="col-auto p-0"> <label for="variable-condition">Переменная</label><select class="form-control" id="variable-condition" @change="setClauseVariable({indexVariable:$event.target.value,indexClause:indexClause,variable:variables[$event.target.value],isInput:isInput})" :value="clause.indexVariable"> <option v-for="(variable,index) in variables" :value="index">{{ variable.name }}</option></select></div><div class="col-auto p-0"> <label for="variable-term-connector">Оператор</label><select class="form-control" id="variable-term-connector" @change="setClauseConnection({indexClause:indexClause,connection:$event.target.value,isInput:isInput})" :value="clause.connection"> <option>=</option><option>≠</option></select></div><div class="col-auto p-0"><label for="term-condition">Термин</label><select class="form-control" id="term-condition" disabled v-if="clause.variable == null"> <option selected>Переменная не выбрана</option></select><select class="form-control" id="term-condition" @change="setClauseTerm({indexClause:indexClause,term:variables[clause.indexVariable].type.terms[$event.target.value],isInput:isInput})" :value="clause.indexTerm" v-else> <option v-for="(term,index) in clause.variable.type.terms" :value="index">{{ term.name }}</option></select></div></div>