<div class="bg-info bg-success bg-opacity-10 p-3" id="rules"><b>Правила</b><i class="bi bi-plus-square text-success mx-3 h4" @click="addRule($store.state.variable)"></i><!--  параметры для rule--><div class="row" v-if="!isEditUpperMethods"><div class="col-auto">Метод активации: {{ruleStore.activator}}</div><div class="col-auto">Метод аккумуляции: {{ruleStore.accumulation}}</div><div class="col-auto"><i class="bi bi-pencil-square text-info mx-3 h4" @click="isEditUpperMethods = !isEditUpperMethods"></i></div></div><div class="row" v-else><div class="col-auto"><label for="activation">Метод активации</label><select class="form-control" @change="setActivator($event.target.value)" :value="ruleStore.activator"><option v-for="name in ruleStore.activators" :key="name" :value="name">{{ name }}</option></select></div><div class="col-auto"><div class="d-flex justify-content-between"><div><label for="algorithm">Метод аккумуляции</label><select class="form-control" @change="setAccumulation($event.target.value)" :value="ruleStore.accumulation"><option v-for="name in ruleStore.accumulations" :key="name" :value="name">{{ name }}</option></select></div></div></div><div class="col-auto"><i class="bi bi-pencil-square text-info mx-3 h4" @click="isEditUpperMethods = !isEditUpperMethods"></i></div></div><!-- end connection, accumulation, activator div--><!--  conditions div --><div class="row my-3" v-for="(rule, iRule) in ruleStore.rules" :key="iRule"><div class="col-auto">{{ rule.name }}:</div>Если<div class="col-auto" v-for="(condition, index) in rule.conditions"><div class="row"><div class="col-auto" v-if="index &gt; 0">{{ condition.conditionConnector }}</div><div class="col-auto">{{ condition.variable.name }} {{ condition.connection }} {{ condition.term.name }}</div></div></div><div class="col-auto">То</div><div class="col-auto"><span v-for="(action, index) in rule.actions"><span v-if="index &gt; 0">,</span>{{ action.variable.name }} {{ action.connection }} {{ action.term.name + " " }}</span></div><div class="col"><div class="d-flex justify-content-between"><span>Весовой коэффициент {{ rule.weight }}</span><span><i class="bi bi-dash-square text-danger h4" @click="ruleStore.rules.splice(iRule, 1)"></i><i class="bi bi-pencil-square text-info h4" @click="changeSelectedRule(rule)"></i></span></div></div><!--  selected rule div--><div class="row my-3 bg-info bg-opacity-10" v-if="ruleStore.selected == rule"><div class="col-auto"><label for="rule-name">Название</label><input class="form-control" id="rule-name" type="text" @input="setRuleName($event.target.value)" :value="ruleStore.selected.name"></div><div class="col-auto"><br><b>Если</b></div><div class="col-auto" v-for="(condition, index) in ruleStore.selected.conditions"><div class="row"><div class="col-auto" v-if="index &gt; 0"><label for="condition-connector">Оператор</label><select class="form-control" id="condition-connector" :value="condition.conditionConnector" @change="setConditionConnector({conditionIndex:index,connector:$event.target.value})"><option>или</option><option>и</option></select></div><div class="col"><clause :variables="$store.state.variable.inputVariables" :indexClause="index" :indexRule="iRule" :isInput="true"></clause></div></div></div><div class="col-auto"><br><i class="bi bi-plus-square text-success h4" @click="addClauseInConditionsSelectedRule($store.state.variable.inputVariables.length)"></i><i class="bi bi-dash-square text-danger h4" @click="deleteLastClauseInConditionsSelectedRule"></i></div><div class="col-auto"><br><b>То</b></div><div class="col-auto p-0" v-for="(action, index) in ruleStore.selected.actions" :key="index"><div class="row"><div class="col"></div><div class="col" v-if="0 &lt; index"><br><b>,</b></div><div class="col-auto"><clause :variables="$store.state.variable.outputVariables" :indexClause="index" :indexRule="iRule" :isInput="false"></clause></div></div></div><div class="col-auto"><br><i class="bi bi-plus-square text-success h4" @click="addClauseInActionsSelectedRule($store.state.variable.outputVariables.length)"></i><i class="bi bi-dash-square text-danger h4" @click="deleteLastClauseInActionsSelectedRule"></i></div><div class="col-auto"><label for="weight-rule">Весовой коэффициент</label><input class="form-control" id="weight-rule" type="number" min="0" max="1" step="0.05" @input="setRuleWeight($event.target.value)" :value="ruleStore.selected.weight"></div></div><hr></div></div>