<div class="col-3 bg-success bg-opacity-10 p-2" id="types"><p class="d-flex justify-content-between"><b>Типы</b><i class="bi bi-plus-square text-success mx-1 h4" data-bs-toggle="modal" data-bs-target="#typeModal"></i></p><!-- список типов--><ul class="list-group"><li class="list-group-item bg-success bg-opacity-10 p-1" v-for="(type, index) in $store.state.type.types" :key="index + 'type'"><!-- тип--><div class="justify-content-between d-flex"><div class="d-flex" style="max-width:50%">Тип:<label class="mx-1" :for="'type' + index" v-if="editableType !== type">{{ type.name }}</label><input class="form-control" v-if="editableType === type" type="text" style="max-width:80%" @input="setTypeName({index:index, name:$event.target.value})" :value="editableType.name"><input class="form-check-input h4 my-0" :id="'type' + index" type="radio" name="chart" style="min-width:24px" :value="type" @click="showChartByType(type)"></div><div><i class="bi bi-arrow-down-square text-muted h4 me-1" data-bs-toggle="collapse" :data-bs-target="'#terms'+index"></i><i class="bi bi-plus-square text-success h4 me-1" data-bs-toggle="modal" data-bs-target="#termModal" @click="addableTypeIndex = index"></i><i class="bi bi-dash-square text-danger h4 me-1" @click="deleteType(index)"></i><i class="bi bi-pencil-square text-info h4 me-1" v-if="editableType!=type" @click="editableType = type"></i><i class="bi bi-pencil-square text-info h4" v-else @click="editableType = null"></i></div></div><!-- список термов в типе--><ul class="collapse list-group" v-if="type.terms.length &gt; 0" :id="'terms'+index"><li class="list-group-item bg-success bg-opacity-10" v-for="(term, indexTerm) in type.terms"><!-- Отображение--><div v-if="$store.state.editableTerm.term != term"><div class="d-flex justify-content-between"><span>Термин: {{ term.name }}</span><span><i class="bi bi-dash-square text-danger h4 me-1" @click="deleteTerm({typeIndex:index, termIndex:indexTerm})"></i><i class="bi bi-pencil-square text-info h4" @click="setEditableTerm(term)"></i></span></div><div>Функция: {{ term.termFunction.name }}</div><div v-if="!term.termFunction.standardDeviation &amp;&amp; !term.termFunction.raw">Абсциссы:<span class="mx-1" v-if="term.termFunction.left">{{ term.termFunction.left }},</span><span class="me-1" v-if="term.termFunction.upperLeft">{{ term.termFunction.upperLeft }},</span><span class="me-1" v-if="term.termFunction.mean"> {{ term.termFunction.mean }},</span><span class="me-1" v-if="term.termFunction.upperRight">{{ term.termFunction.upperRight }},</span><span class="me-1" v-if="term.termFunction.right"> {{ term.termFunction.right }}</span><span class="me-1" v-if="term.termFunction.x"> {{ term.termFunction.x }}</span></div><div v-if="term.termFunction.standardDeviation">Среднее: {{ term.termFunction.mean }}</div><div v-if="term.termFunction.standardDeviation">Стандартное отклонение: {{ term.termFunction.standardDeviation }}</div><div v-if="term.termFunction.y">Координаты: {{ term.termFunction.raw }}</div></div><!-- изменение--><div v-else><div class="d-flex justify-content-between"><label for="TermName">Название</label><input class="form-control" id="TermName" type="text" style="max-width:50%" :value="$store.state.editableTerm.term.name" @input="setTermName({indexType:index, indexTerm:indexTerm, name:$event.target.value})"><span><i class="bi bi-dash-square text-danger h4" @click="deleteTerm({typeIndex:index, termIndex:indexTerm})"></i><i class="bi bi-pencil-square text-info h4" @click="setEditableTerm(null)"></i></span></div><EditableFunction></EditableFunction></div></li></ul><div class="alert alert-danger" v-if="type.terms.length === 0">Отсутствуют термины</div></li></ul><!-- модальное окно для добавления типов--><form class="modal fade" id="typeModal" tabindex="-1" aria-labelledby="typeModalLabel" aria-hidden="true" @submit.prevent="addTypeName(addableTypeName)"><div class="modal-dialog"><div class="modal-content"><div class="modal-header"><h4 class="modal-title" id="typeModalLabel">Добавление типа</h4><button class="btn-close" type="button" data-bs-dismiss="modal" aria-label="Close"></button></div><div class="modal-body"><label for="nameType">Введите название</label><input class="form-control" id="nameType" type="text" required v-model="addableTypeName" placeholder="Название"></div><div class="modal-footer"><button class="btn btn-success container-fluid">Добавить</button></div></div></div></form><!-- модальное окно для добавления терминов--><form class="modal fade" id="termModal" tabindex="-1" aria-labelledby="termModalLabel" aria-hidden="true" @submit.prevent="addTerm({term:$store.state.addableTerm,typeIndex:addableTypeIndex})"><div class="modal-dialog"><div class="modal-content"><div class="modal-header"><h4 class="modal-title" id="termModalLabel">Добавление термина</h4><button class="btn-close" type="button" data-bs-dismiss="modal" aria-label="Close"></button></div><div class="modal-body"><label for="nameTerm">Введите название</label><input class="form-control" id="nameTerm" type="text" required :value="$store.state.addableTerm.name" @input="setNameAddableTerm($event.target.value)" placeholder="Название"><Function></Function></div><div class="modal-footer"><button class="btn btn-success container-fluid">Добавить</button></div></div></div></form></div>