<div class="p-2"><div class="my-2 row justify-content-between me-5"><div class="col-auto d-flex"><div><input class="form-file-input" id="customFile" type="file" hidden @change="$store.dispatch('importFCL',$event.target.files[0])"><label class="form-file-label btn btn-success" for="customFile" style="cursor: pointer"> Импортировать FCL (только латинский алфавит)</label></div></div><div class="form-check form-switch col-auto"><input class="form-check-input" id="isColor" @change="$store.commit('setColor')" type="checkbox" role="switch"><label class="form-check-label" for="isColor">Цветные графики</label></div></div><div class="row m-0"><VariablesContent></VariablesContent><ChartContent v-show="!$store.state.isLoading" ref="chart"></ChartContent><div class="spinner-border" v-show="$store.state.isLoading" role="status"><span class="visually-hidden">Загрузка...</span></div><TypesContent></TypesContent></div><RulesContent></RulesContent><div class="row row-cols-auto my-2 justify-content-center" id="buttons"><div class="col"><button class="btn btn-success" type="button" @click="getOutputValues"> Получить значения для выходных данных</button></div><div class="col"><button class="btn btn-success" type="button" @click="$store.dispatch('exportFCL')"> Экспортировать в FCL</button></div><div class="col"><button class="btn btn-success" type="button" @click="$store.dispatch('export')">Экспортировать в TXT</button></div></div></div>