<div class="container my-3"><h3>Описание приложения </h3><p>Visual Fuzzy Control Language - приложение для моделирования систем нечеткой логики.</p>Возможности:<ul class="list-group"><li class="list-group-item">Моделирование системы нечеткой логики</li><li class="list-group-item">Импортирование модели в формате FCL(ГОСТ Р МЭК 61131-7—2017)</li><li class="list-group-item">Экспортирование модели в формате FCL(ГОСТ Р МЭК 61131-7—2017)</li><li class="list-group-item">Экспортирование модели в формате TXT (без стандарта)</li><li class="list-group-item">Вычисление значений выходных переменных по алгоритму Мамдани</li></ul><a :href="$store.state.url+'/gost_r_mek.pdf'">Ссылка на ГОСТ Р МЭК 61131-7—2017</a><br><a :href="$store.state.url+'/tipper.fcl'">Ссылка на образец файла с форматом FCL</a><br><a href="https://jfuzzylogic.sourceforge.net/html/manual.html">Ссылка на библиотеку с описанием формата FCL в файле</a><h4 class="my-4">Инструкция по использованию приложения</h4>Вы можете загрузить свою модель на латинице в формате FCL или создать собственную модель.
Ниже описаны шаги по созданию модели нечеткой логики<ul class="list-group"><li class="list-group-item">1. Добавление типа, которые включает задание названия.</li><li class="list-group-item">2. Добавление терма: задание имени, выбора функции принадлежности с последующим вводом ее параметров. </li><li class="list-group-item">3. Добавление входных и выходных переменных.<br>Для входных переменных: задание имени переменной, выбора типа, ввода значения.<br>Для выходных переменных: задание имени переменной, выбора типа, метода дефаззификации, ввода значения по умолчанию.</li><li class="list-group-item">4. Добавление правил.<br>Правило добавляет в конец списка. 
Правила автоматически получают свое название, которые можете изменить. 
Правило задается из условия, оператора и весового коэффициента. 
Условие состоит из переменной, оператора равенства, терма данного равенства.
Условия имеют одинаковую структуру с операторами, за исключением, 
что в условиях используются входные переменных, а операторах выходные.</li><li class="list-group-item">5. Выбора в блоке правил метода аккумуляции и активации. <br>По умолчанию для связи "и" используется минимум, а для "или" максимум.</li><li class="list-group-item">6. Получения значений для выходных переменных по нажатию на кнопку.<br>Автоматически будут выведены графики функций принадлежности с найденным значением для последней выходной переменной.</li><li class="list-group-item">7. Для повторного использования созданной модели необходимо экспортировать ее в формате FCL.<br>Экспортирование в формате TXT имеет немного другое оформление, которое немного легче воспринимается. 
Однако импортирование для данного формата не предусмотрено.</li></ul><h4 class="my-3">Пользовательский интерфейс</h4>Пользовательский интерфейс разделен на несколько блоков: правила, типы, графики функций принадлежности, переменные.
Они имеют следующие элементы для пользовательского взаимодействия с системой:<ul class="list-group"><li class="list-group-item"> <i class="bi bi-plus-square text-success h5 me-2"></i>- кнопка для добавления. Создает модальное окно для добавления терма, переменной, типа. 
Добавляет в конец списка новое правило, условие и оператор правила.</li><li class="list-group-item"> <i class="bi bi-dash-square text-danger h5 me-2"></i>- кнопка для удаления. Удаляет соответствующий элемент из списка.</li><li class="list-group-item"> <i class="bi bi-pencil-square text-info h5 me-2"></i>- кнопка для редактирования. Позволяет редактировать соответствующий элемент в списке.</li><li class="list-group-item"> <i class="bi bi-arrow-down-square text-muted h5 me-2"></i>- кнопка для скрытия. Дает возможность скрыть список.</li><li class="list-group-item"><div class="form-check form-switch"><input class="form-check-input" id="isColor1"><label class="form-check-label me-2" for="isColor1">Цветные графики</label>- переключатель серых графиков на разноцветные.</div></li><li class="list-group-item">Тип:<label class="mx-1" for="food">Еда</label><input class="form-check-input h4 my-0 me-2" id="food" type="radio" style="min-width:24px">- переключатель для выбора типа или переменной, для отображения графиков функций принадлежности с заданным значением переменных.</li><li class="list-group-item"><button class="btn btn-success me-2" type="button"> Получить значения для выходных данных</button>- кнопка для получения значений выходных переменных</li><li class="list-group-item"><button class="btn btn-success me-2" type="button"> Импортировать fcl (только латинский алфавит)</button>- кнопка для загрузки из файла модели в формате FCL</li><li class="list-group-item"><button class="btn btn-success me-2" type="button"> Экспортировать в FCL</button>- кнопка для скачивания файла созданной модели в формате FCL </li><li class="list-group-item"><button class="btn btn-success me-2" type="button"> Экспортировать в TXT</button>- кнопка для скачивания файла созданной модели в формате TXT</li></ul></div>