<div class="col-3 bg-success bg-opacity-10 p-2" id="types"><div class="d-flex justify-content-between"><b>Входные переменные</b><i class="bi bi-plus-square text-success h4" data-bs-toggle="modal" data-bs-target="#modal" @click="isAddableInputVariable = true"></i></div><!-- список входных данных--><ul class="list-group"><li class="list-group-item bg-success bg-opacity-10 p-1 d-flex justify-content-between" v-for="(variable, index) in $store.state.variable.inputVariables"><!-- отображение данных--><div v-if="editableVariable !== variable"><div><label :for="'selectedInputVariable' + index"> </label>Переменная: {{ variable.name }}<input class="mx-1 form-check-input h4 my-0" :id="'selectedInputVariable' + index" type="radio" name="chart" @click="showChartByVariable(variable)"></div><div>Тип: {{ variable.type.name }}</div><div>Значение: {{ variable.value }}</div></div><!-- изменение данных--><div v-if="editableVariable === variable"><label for="vName">Название переменной</label><input class="form-control" id="vName" type="text" :value="editableVariable.name" @input="setNameInputVariable({value:$event.target.value, index:index})"><label for="vType">Выберете тип переменной</label><select class="form-select" id="vType" :value="editableVariable.type.index" @change="setTypeInputVariable({type:$store.state.type.types[$event.target.value], variableIndex:index})"><option v-for="type in $store.state.type.types" :value="type.index">{{ type.name }}</option></select><label for="vValue">Значение</label><input class="form-control" id="vValue" type="number" :value="editableVariable.value" @input="setValueInputVariable({value:$event.target.value, index:index})"></div><!--  Блок, если нет данных --><div class="alert alert-danger" v-if="$store.state.variable.inputVariables.length === 0"> Отсутствуют входные данные</div><!--  Блок действий с переменными --><span id="actionsInputVariables" style="min-width:52px"><i class="bi bi-dash-square text-danger h4 me-1" @click="deleteInputVariable(index)"></i><i class="bi bi-pencil-square text-info h4" @click="changeEditable(variable)"></i></span></li></ul><div class="d-flex justify-content-between"><b>Выходные переменные</b><i class="bi bi-plus-square text-success h4" data-bs-toggle="modal" data-bs-target="#modal" @click="isAddableInputVariable = false"></i></div><!-- список выходных данных--><ul class="list-group"><li class="list-group-item bg-success bg-opacity-10 p-1 d-flex justify-content-between" v-for="(variable, index) in $store.state.variable.outputVariables"><div v-if="editableVariable !== variable"><div><label :for="'selectedOutputVariable' + index">Переменная: {{ variable.name }}</label><input class="mx-1 form-check-input h4 my-0" :id="'selectedOutputVariable' + index" type="radio" name="chart" @click="showChartByVariable(variable)"></div><div>Тип: {{ variable.type.name }}</div><div>Метод дефаззификации:<br>{{ variable.method.name }}</div><div>Значение поумолчанию: {{ variable.def }}</div><div v-if="variable.value">Значение: {{ variable.value }}</div></div><div v-if="editableVariable === variable"><input class="form-control" type="text" :value="editableVariable.name" @input="setNameOutputVariable({value:$event.target.value, index:index})"><select class="form-select" :value="editableVariable.type.index" @change="setTypeOutputVariable({type:$store.state.type.types[$event.target.value], index:index})"><option v-for="varType in $store.state.type.types" :key="varType.name" :value="varType.index">{{ varType.name }}</option></select><label for="vmethod">Метод дефаззификации</label><select class="form-select" id="vmethod" :value="editableVariable.method.index" @change="setMethodOutputVariable({method:methods[$event.target.value], index:index})"><option v-for="method in methods" :key="method.name" :value="method.index">{{ method.name }}</option></select><input class="form-control" type="text" :value="editableVariable.def" @input="setDefOutputVariable({value:$event.target.value, index:index})"></div><!--  Блок Отсутствуют выходных переменных --><div class="alert alert-danger" v-if="$store.state.variable.outputVariables.length === 0">Отсутствуют выходные данные</div><!--  Действия с выходными переменными --><span id="actionsOutputVariables" style="min-width:52px"><i class="bi bi-dash-square text-danger h4 me-1" @click="deleteOutputVariable(index)"></i><i class="bi bi-pencil-square text-info h4" @click="changeEditable(variable)"></i></span></li></ul><!-- модальное окно для добавления входных/выходных переменных--><form class="modal fade" id="modal" tabindex="-1" aria-labelledby="modalLabel" aria-hidden="true"><div class="modal-dialog"><form class="modal-content" @submit.prevent="isAddableInputVariable ? addInputVariable(addableVariable) : addOutputVariable(addableVariable)"><div class="modal-header"><h4 class="modal-title" id="modalLabel" v-if="isAddableInputVariable">Добавление входной переменной</h4><h4 class="modal-title" id="modalLabel" v-else>Добавление выходной переменной</h4><button class="btn-close" type="button" data-bs-dismiss="modal" aria-label="Close"></button></div><div class="modal-body"><div class="row"><div class="col"><label for="nameOV">Название переменной</label><input class="form-control" id="nameOV" type="text" required v-model="addableVariable.name" placeholder="Название"></div><div class="col" v-if="isAddableInputVariable"><label for="default">Значение </label><input class="form-control" id="default" required v-model="addableVariable.value" type="number"></div><div class="col" v-if="!isAddableInputVariable"><label for="default">Значение поумолчанию</label><input class="form-control" id="default" required v-model="addableVariable.def" type="number"></div></div><div class="row"><div class="col" v-if="!isAddableInputVariable"><label for="method">Метод деффазификации</label><select class="form-select" id="method" v-model="addableVariable.method" required><option v-for="method in methods" :key="method.name" :value="method">{{ method.name }}</option></select></div><div class="col"><label for="typeOV">Тип переменной</label><select class="form-select" id="typeOV" v-model="addableVariable.type" required><option v-for="varType in $store.state.type.types" :key="varType.name" :value="varType">{{ varType.name }}</option></select></div></div></div><div class="modal-footer"><button class="btn btn-success container-fluid">Добавить</button></div></form></div></form></div>